<template>
  <v-layout row>
    <v-flex xs12 >
      <v-card>
        <v-list three-line>
          <template v-for="(item, index) in items">
            <v-subheader
              v-if="item.header"
              :key="item.header"
            >
              {{ item.header }}
            </v-subheader>

            <v-divider
              v-else-if="item.divider"
              :inset="item.inset"
              :key="index"
            ></v-divider>

            <v-list-tile
              v-else
              :key="item.title"
              avatar
              @click="dialog = true"
            >
              <v-list-tile-avatar>
                <img :src="item.avatar">
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>

    <v-dialog
      v-model="dialog"
      max-width="750" v-for="item in items" :key="item.title"
    >
      <v-card>
        <video width="100%" controls controlsList="nodownload" :src="item.video"></video>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
        </v-card-actions>        
      </v-card>
    </v-dialog>

    </v-flex>
  </v-layout>
</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'

var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX192B1HFM4Xww8tQKXwNesDapwVYiaqm50CVbbyCxokXhYlUheJGl3FZ7lG0tHCXPRPdZ9ABVhy+WTLASLnDzV1GsMDjIH734XtFVY1sOXDE4Irun88vDvEhLf78tO554jF2vrxGi0iULBgGGbvIZrK63fcPsl3M3QQ="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

export default {
  name: 'Home',
  computed: {
     itemsFilter: function() {
       var textSearch = this.textSearch;
       return this.items.filter(function(el) {
         return el.name.toLowerCase().indexOf(textSearch.toLowerCase()) !== -1;
       });
     }
  },
  methods: {
    getItems() {
      this.$http.get(url)
//      axios.get(url) 
        .then((response) => {
          this.items = response.data;
        })
       .catch((error) => {
          this.items = error.response.statusText;
        });
    }
  },  
  created () {
		this.getItems()    
  },
  data: () => ({
    dialog: false,     
    textSearch: "",  
    items: []    
  })
}
</script>

